import Contacto from "../Contacto/Contacto";
import "./Servicios.css";
import icono_solo from "../../imagenes/iso solo.png";
import icono_foto from "../../imagenes/icono podcast.png";
import Navbar2 from "../../components/Navbar/Navbar2";

function Podcast(props) {
    return (
        <>
            <Navbar2/>
            <div className="d-flex flex-column align-items-center img_fondo">
                <div className="seccion">
                    <h1>Servicios</h1>
                    <h3>Asesora</h3>
                </div>
                <img
                    src={ icono_solo }
                    alt="noe-bareille-comunicadora digital"
                    className="icono_fondo_solo"
                />
            </div>
            <div className="img_1">
                <img src={ icono_foto } alt="Noe Bareille"/>
            </div>
            <div className="body_margin">
                <div className="d-flex text-start">
                    <div className="section_margin">
                        <h3>Edicin de podcast</h3>

                        <p className="my-5">
                            Quers empezar con tu podcast pero no sabs editar o no conts
                            con el tiempo para hacerlo?
                        </p>
                        <p className="my-5">

                            Edito tu podcast y producimos juntos/as una apertura, separadores
                            y cierre para tu programa.
                        </p>
                        <p className="my-5">
                            En el ltimo tiempo el podcast se ha popularizado, esto es en parte porque es un contenido
                            que permite al oyente poder realizar otras actividades mientras escucha. Grabar un podcast y
                            distribuirlo es gratuito por lo cual es una buena forma de promocionar tus productos o
                            servicios. El formato permite que pueda monetizar tu contenido a travs de sponsors y
                            publicidades.

                            Crear contenido de valor con frecuencia es MUY IMPORTANTE para crear comunidad y nada mejor
                            que un podcast para conectar con tu audiencia.

                            Quers empezar con tu podcast pero no sabs editar o no conts con el tiempo para hacerlo?

                            No te preocupes, edito tu podcast y producimos juntos/as una apertura, separadores y cierre
                            para tu programa.
                        </p>

                        <a href="http://bit.ly/3IKGOk2" type="button" className="btn button_style mt-4" target="_blank">
                            Si, quiero consultar
                        </a>
                    </div>
                </div>
            </div>
            <Contacto/>
        </>
    );
}

export default Podcast;
